/* Custom styling for Calculator dropdown nav button*/

.nav_styles_calc__1wfWX {
  background-color: transparent !important;
  /* color: #007bff !important; */
  border: none !important;
  color: white !important;
  border: none !important;
  font-size: 20px;
  opacity: 0.6;
  transition: 0.3s;
  margin-left: 30px;
  text-decoration: none;
}

.nav_styles_calc__1wfWX:hover {
  background-color: transparent !important;
  color: #ffbb33 !important;
  border: none !important;
  opacity: 1;
  text-decoration: none;
}

.nav_styles_calc__1wfWX:active {
  background-color: transparent !important;
  color: #17a2b8 !important;
  border: none !important;
  transition: none !important;
}

.nav_styles_calc__1wfWX::after {
  background-color: transparent !important;
  color: #17a2b8 !important;
  border: none !important;
  transition: none !important;
}

.nav_styles_calc__1wfWX::selection {
  background-color: transparent !important;
  color: #17a2b8 !important;
  border: none !important;
  transition: none !important;
}

.nav_styles_calc__1wfWX:default {
  background-color: transparent !important;
  color: #17a2b8 !important;
  border: none !important;
  transition: none !important;
}

.nav_styles_calc__1wfWX:focus {
  outline: none !important;
  box-shadow: none !important;
}

.nav_styles_homeButton__1AILS {
  text-decoration: none;
  color: white !important;
  opacity: 0.6;
  transition: 0.3s;
  font-size: 25px;
}

.nav_styles_homeButton__1AILS:hover {
  text-decoration: none;
  color: #17a2b8 !important;
  opacity: 0.6;
  opacity: 1;
}

